  const Icon = ({ href, icon }) => (
    <a
      target="_blank"
      href={href}
      rel="external nofollow noopener noreferrer"
      className="contact-icon"
    >
      <span className="fa-layers fa-fw fa-2x">
        <FontAwesomeIcon icon={icon} />
      </span>
    </a>
  );

